<template>
  <div>
    <div id="leixin">
      <!-- <h3 class="title_container">商品库存查询（明细）</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params">
          <el-form-item label="库存组织" class="organization" size="mini">
            <el-select clearable v-model="companyidList" multiple collapse-tags @focus="companyListInvok" placeholder="请选择" filterable @change="companyChange">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <br>
          <el-form-item label="商品编号" size="mini">
            <el-input v-model="params.goods_id" clearable placeholder="请输入" @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" clearable placeholder="请输入" @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" filterable placeholder="请选择" @focus="warehouseInvok" @change="warehouseChange">
              <el-option v-for="item in warehouseList" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
                <span style="float: left">{{ item.warehouse_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位" size="mini">
            <erpAllocation v-model="params.allocation_id" :disabled='!params.warehouse_id' :searchParams="{company_id: params.company_id, warehouse_id: params.warehouse_id, is_incomplete: 3}" />
          </el-form-item>
          <el-form-item label="品牌" size="mini">
            <getBrand v-model='params.brand_id' />
          </el-form-item>
          <el-form-item label="类型" size="mini">
            <jybd-cascader ref="cascader" @cascaderChange="handleChange"></jybd-cascader>
          </el-form-item>
          <el-form-item label="工厂代码" size="mini">
            <el-input v-model="params.factory_code" placeholder="请输入" @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item size="mini">
            <!-- 默认显示库存不为0 传1显示库存为0	 -->
            <el-checkbox v-model="availableStocksFlag" style="margin:0px 22px 0 62px">包含库存为0的商品</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>

        </el-form>
      </div>
      <div class="add_delet">
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!-- <el-button plain @click="exportFile" icon="iconfont icon-daochu10" size="mini" v-if="authority.b_export==1">导出</el-button> -->
      </div>
      <div class="table_container">
        <xTable :isShowTool='true' :ajax='{ url: "getGoodsInventoryListV2", params: params }' fieldUrl='v2/inventory/InventoryGoods/getGoodsInventoryList' :sign="indexSign" :rowClassName='{name: "sign_row",isDisable: false}' ref='xTable' :footerMethod='footerMethod' :tableColumn='tableColumn' />
      </div>
    </div>
  </div>
</template>

<script>
import { exportFile } from "common/utils/public_function/commonUtils";
import { tableHeader } from "./config/inventorygoods";
export default {
  name: "inventoryInventorygoods",
  mixins: [tableHeader],
  data() {
    return {
      showAllocationList: true,
      availableStocksFlag: 0,
      companyidList: [],
      TableData: {
        tableData: [],
        allList: {},
        total: 10,
      },
      loading:false,
      loadingsum: true,
      TableDataSum: {},

      params: {
        export_type: 1,
      },
    };
  },
  methods: {
    companyChange(val) {
      this.$set(this.params, "company_id", val.join());
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.params, "allocation_id", "");
    },

    handleChange(val) {
      this.params.goods_type_id = val[2];
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$refs.cascader.clear();
      this.$set(this, "params", {});
      this.companyidList = [];
      this.availableStocksFlag = null;
      this.allocationInventoryList = null;
      this.onSearch(1);
    },
    warehouseChange(val) {
      this.showAllocationList = false;
      this.$set(this.params, "allocation_id", 0);
    },
    onSearch(number) {
      this.params.stocks = this.availableStocksFlag ? 1 : 0;
      this.$axMethod.getAllGoodsInventoryList(this.params).then((response) => {
        if (response.data.code == 200) {
          this.footerData = response.data.data;
          this.$refs.xTable._proxy("updateFooter");
        }
      });
      this.$refs.xTable._getData(number);
      console.log(this.params);
    },
    exportFile() {
      //导出
      let arg = {
        params: this.params,
        API: "v2ExportGoodsInventoryUrl",
      };
      exportFile(arg);
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod.exportGoodsInventoryUrl(s).then((res) => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.onSearch();
  },
};
</script>

