export const tableHeader = {// 需要传进来一个this对象
  data() {
    return {
      footerData: {},
      tableColumn: [
        {
          type: 'seq',
          title: '序号',
          width: 50
        },
        {
          title: '库存组织',
          field: 'company_name',
          width: 180
        },
        {
          title: '商品编号',
          field: 'goods_id',
          width: 110
        },
        {
          title: '商品名称',
          field: 'goods_name',
          width: 400
        },
        {
          title: '工厂代码',
          field: 'factory_code',
          width: 150
        },
        {
          title: '年周/批次',
          field: 'batch',
          width: 100
        },
        {
          title: '生产日期',
          field: 'production_time',
          width: 80,
          slots: {
            header: () => {
              return [<el-tooltip class="item"
                content="Bottom center"
                effect="light" >
                <p slot="content" > 生产日期列后期将会隐藏， 同时新增商品入库日期列 </p> < span class="el-icon-question"
                  style="cursor:pointer;font-weight:bold;" > 生产日期 </span> </el-tooltip>
              ]
            }
          }
        },
        {
          title: '仓库名称',
          field: 'warehouse_name',
          width: 132
        },
        {
          title: '货位',
          field: 'allocation_name',
          width: 100
        },
        {
          title: '货位数量',
          field: 'stocks',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          width: 80
        },
        {
          title: '成本价',
          field: 'cost_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          formatter: 'priceFormat',
          width: 120
        },
        {
          title: '销售价',
          field: 'sale_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          formatter: 'priceFormat',
          width: 120
        },
        {
          title: '成本金额',
          field: 'subtotal_amount',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          formatter: 'priceFormatNone',
          width: 120
        },
        {
          title: '是否为实物库存',
          field: 'is_entity',
          width: 120
        }
      ]
    }
  },
  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "页小计";
          }
          if (["stocks", "subtotal_amount"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "subtotal_amount") {
              return num
                .toFixed(2)
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            if (column.property === "stocks") {
              return num
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            return Number.isInteger(num) ? num : num.toFixed(2);
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "合计";
          }
          if (column.property === 'stocks' && this.footerData.total_stocks) {
            return this.footerData.total_stocks
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          }
          if (column.property === 'subtotal_amount' && this.footerData.total_subtotal_amount) {
            return (this.footerData.total_subtotal_amount - 0)
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          }
          if (["stocks", "subtotal_amount"].includes(column.property)) {
            return '加载中...'
          }
          return null;
        })]
    }
  }
}